import * as React from "react"
import {Link} from "gatsby";
import Layout from "../components/layout";
import {formatDate} from "../utils/date";
import {SignupForm} from "../components/signup-form";


const CodelabsPage = () => {

    const labs = [
        {
            title: "Hello World",
            slug: "hello-world",
            publishedAt: "2021-05-01T00:00:00Z",
            brief: "A simple hello world program",
            tech: "python",
            level: "beginner"
        },
        {
            title: "Test World",
            slug: "test-world",
            publishedAt: "2021-05-01T00:00:00Z",
            brief: "A simple test world program",
            tech: "javascript",
            level: "beginner"
        }
    ]

    return (
        <Layout>
            <ul className={"p-0"}>
                {labs.map((lab: any) => (
                    <li style={{display: "block"}} key={lab.slug}>
                        <Link style={{ boxShadow: `none` }} to={`/codelabs/${lab.slug}`}>
                            <h2 style={{ marginBottom: `0.2rem` }}>
                                {lab.title}
                            </h2>
                        </Link>
                        <small>{formatDate(lab.publishedAt)}</small>
                        <p style={{ marginTop: `0.5rem` }}>{lab.brief}</p>
                    </li>
                ))}
            </ul>
            <br />
            <SignupForm />
            <br />
        </Layout>
    )
}

export default CodelabsPage